<style scoped>
    .form {
        padding: 20px;
    }
    .demo-upload-list{
        display: inline-block;
        height: 150px;
        text-align: center;
        line-height: 150px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 4px;
    }
    .demo-upload-list img{
        height: 100%;
    }
    .demo-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .demo-upload-list:hover .demo-upload-list-cover{
        display: block;
    }
    .demo-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
    #editor {
        min-height: 500px;
    }
</style>
<template>
    <div class="page">
        <div class="layout-breadcrumb">
            <Breadcrumb>
                <Breadcrumb-item></Breadcrumb-item>
                <Breadcrumb-item></Breadcrumb-item>
                <Breadcrumb-item></Breadcrumb-item>
            </Breadcrumb>
        </div>
        <div class="layout-content">
            <i-form ref="form" class="form" :model="formModel" :rules="formRules" :label-width="80" label-position="left">
                <Form-item label="" prop="title">
                    <i-input v-model="formModel.title" placeholder=""></i-input>
                </Form-item>
                <Form-item label="banner">
                    <div v-if="uploadList.length > 0" v-for="item in uploadList" class="demo-upload-list">
                        <template v-if="item.status === 'finished'">
                            <img :src="item.url">
                            <input type="hidden" name="front_cover" :value="item.name" />
                            <div class="demo-upload-list-cover">
                                <Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
                                <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                            </div>
                        </template>
                        <template v-else>
                            <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
                        </template>
                    </div>
                    <Upload
                        ref="upload"
                        :show-upload-list="false"
                        :headers="uploadHeaders"
                        :format="['jpg','jpeg','png']"
                        :max-size="2048"
                        :on-format-error="handleFormatError"
                        :on-exceeded-size="handleMaxSize"
                        :before-upload="handleBeforeUpload"
                        :on-progress="handleProgress"
                        :on-success="handleSuccess"
                        type="drag"
                        :action="uploadUrl"
                        style="display: inline-block;width:58px;">
                        <div style="width: 58px;height:58px;line-height: 58px;">
                            <Icon type="camera" size="20"></Icon>
                        </div>
                    </Upload>
                    <Modal title="" v-model="visible">
                        <img :src="viewUrl" v-if="visible" style="width: 100%">
                    </Modal>
                </Form-item>
                <Form-item label="" prop="desc">
                    <Input v-model="formModel.desc" type="textarea" placeholder="300"></Input>
                </Form-item>
                <Form-item label="" prop="content">
                    <div id="editor" v-html="formModel.content"></div>
                </Form-item>
                <Form-item label="">
                    <Radio-group v-model="formModel.is_show">
                        <Radio label="1"></Radio>
                        <Radio label="0"></Radio>
                    </Radio-group>
                </Form-item>
                <Form-item>
                    <Button type="primary" @click="submit"></Button>
                    <router-link to="/post">
                        <Button type="ghost" style="margin-left: 8px"></Button>
                    </router-link>
                </Form-item>
            </i-form>
        </div>
    </div>
</template>
<script>
    import 'wangeditor/dist/css/wangEditor.min.css';
    import WangEditor from 'wangeditor';
    export default {
        data () {
            return {
                uploadUrl: config.getApi(config.api.upload_pic),
                loading: false,
                formModel: {
                    is_show: 1
                },
                uploadList: [],
                imgName: '',
                visible: false,
                uploadHeaders: {
                    Authorization: config.getToken()
                },
                formRules: {
                    title: [
                        {required: true, message: ' ', trigger: 'blur'}
                    ],
                    desc: [
                        { type: 'string', max: 300, message: '300', trigger: 'blur' }
                    ],
                    content: [
                        {required: true, message: ' ', trigger: 'blur'}
                    ]
                }
            }
        },
        created () {

        },
        beforeDestroy () {
            this.destroyEditor();
        },
        methods: {
            getData (id) {
                this.$http.get(config.getApi(config.api.post.show(id))).then(response => {
                    this.formModel = response.body.data;
                    const frontCover = this.formModel.front_cover;
                    if (frontCover) {
                        this.initFile({name: frontCover, url: config.img_url(frontCover), status: 'finished'});
                    }
                }, response => {
                    this.loading = false;
                });
            },

            handleView (name) {
                this.imgName = name;
                this.visible = true;
            },
            handleRemove (file) {
                this.clearFiles();
            },
            handleBeforeUpload (file)
            {
                this.clearFiles();
            },
            handleFormatError (file) {
                this.$Notice.error({
                    title: '',
                    desc: ' ' + file.name + '  jpg  png '
                });
            },
            handleMaxSize (file) {
                this.$Notice.error({
                    title: '',
                    desc: ' ' + file.name + '  2M'
                });
            },
            handleProgress (event, file, fileList) {
                this.initFile(file);
            },
            handleSuccess (res, file, fileList) {
                file.name = res.data.path;
                file.url = config.img_url(file.name);

                this.initFile(file);
                this.formModel.front_cover = file.name;
            },
            initFile (file) {
                this.uploadList = [];
                this.uploadList.push(file);
                this.formModel.front_cover = file.name;
            },
            clearFiles () {
                this.$refs.upload.clearFiles();
                this.uploadList = [];
                this.formModel.front_cover = '';
            },

            createEditor () {
                // 
                this.editor = new WangEditor('editor');
                this.initEditorConfig();
                this.editor.create();
                // 
                this.editor.$txt.html(this.formModel.content);
            },
            destroyEditor () {  // 
//                this.editor.destroy();  // 
                // $('editor').remove();  // 
//                this.editor = null;
                WangEditor.numberOfLocation--;  // 
            },
            initEditorConfig () {
                this.editor.config.uploadImgUrl = config.getApi(config.api.upload_pic);  // 
                this.editor.config.uploadImgFileName = 'file';  // namename
                this.editor.config.uploadHeaders = {
                    'Authorization': config.getToken()
                };

                // load
                var _this = this;
                this.editor.config.uploadImgFns.onload = function (resultText, xhr) {
                    // resultText text
                    // xhr  xmlHttpRequest IE89
                    var result = JSON.parse(resultText);
                    var imgUrl = config.img_url(result.data.path);
                    //  resultText url
                    _this.editor.command(null, 'insertHtml', '<img src="' + imgUrl + '" style="max-width:100%;"/>');
                    //  img  max-width 
                    // editor.command(null, 'InsertImage', resultText);
                };
            },

            submit () {
                this.formModel.content = this.editor.$txt.html();
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        var method = '';
                        var api = '';
                        if (this.formModel.id > 0) {
                            method = 'put';
                            api = config.getApi(config.api.post.update(this.formModel.id));
                        } else {
                            method = 'post';
                            api = config.getApi(config.api.post.create);
                        }
                        this.$http[method](api, this.formModel).then(response => {
                            this.$Message.success('');
                            this.$router.push('/post');
                        }, response => {
                            this.loading = false;
                        });
                    } else {
                        this.$Message.error('');
                    }
                })
            }
        },
        mounted () {
            if (this.$route.params.id > 0) {
                this.getData(this.$route.params.id);
            }
            this.createEditor();
        },
        computed: {
            viewUrl: function () {
                return config.img_url(this.imgName);
            }
        }
    }
</script>



